* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.search {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgb(0, 0, 0, 0.2);
}

input,
button {
  padding: 0.5rem 1rem;
  margin-left: 10px;
  font-size: 1rem;
}

.cityHead {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cityName {
  text-transform: uppercase;
}
